
body {
    margin: 0;
    padding: 0;
    /*overflow: hidden; /* Hide scrollbars */
    position: relative; /* Ensure overlay covers entire viewport */
    transition: opacity 1s ease-in; /* Apply transition to the entire body */
}

#overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent black overlay */
    z-index: 1; /* Place overlay above video and below content */
}

#video-bg {
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    transform: translate(-50%, -50%);
    z-index: -1; /* Place the video behind other content */
}

.content {
    position: relative;
    z-index: 2;
    text-align: center;
    color: #d2d2d2;
    opacity: 1;
    transition: opacity 1s ease; /* Add transition for opacity */
}

.fade-in {
    opacity: 1;
}

h1 {
    margin-top: 10px; 
    margin-bottom: 0px; 
    text-align: center;
    color: #fff;
    font-size: 55px;
}
.container {
    width: 80%;
    margin: auto;
    
}

.input-container {
    text-align: center;
    margin-top: 20px; 
    border-radius:20%;
}

.input-container label {
    font-size: 25px;
}

.input-container input[type="text"],
.input-container button {
    padding: 15px 30px;
    font-size: 20px;
    border-radius: 50px;
    background-color: #e9e9e9;
    color: #0458ad;
    cursor: pointer;
    transition: background-color 0.3s ease-in, transform 0.3s ease;
}
/* Startt.css */
/* Add loading animation */
@keyframes spin {
    to { transform: rotate(360deg); }
  }
  
  /* Add loading class to button */
  #submitBtn.loading::after {
    content: "";
    
    width: 20px;
    height: 20px;
    border: 2px solid #f3f3f3;
    border-top: 2px solid #3498db;
    border-radius: 50%;
    display: inline-block;
    animation: spin 5s infinite;
    margin-left: 5px;
  }
  
  /* Add other styles for your components here */
  
.input-container input[type="text"] {
    width: 300px;
    margin-right: 20px;
}

.input-container button:hover {
    background-color: #0458ad;
    color: white;
    border-radius: 50px;
}

.or-divider {
    text-align: center;
    margin: 20px 0;
    font-size: 25px;
}

.or-divider::before,
.or-divider::after {
    content: "";
    display: inline-block;
    width: 40px;
    border-top: 2px solid #000;
    margin: 0 10px;
    vertical-align: middle;
}


.or-divider::before {
    margin-left: 0;
    margin-right: 10px;
}

.or-divider::after {
    margin-right: 0;
    margin-left: 10px;
}

/* Style the semi-transparent black overlay */
#video-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black overlay */
    z-index: 1; /* Place the overlay above the video */
    display: none; /* Initially hide the overlay */
}

#myChart {
    margin-top: 20px;
   /* width: 1000px;/* Set the desired width */
    height: auto; 
}
#emotionText {
    font-size: 30px; /* Adjust the font size as needed */
}

.button {
    margin-top: 50px;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 70px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    transition: transform 0.3s ease;
    overflow: hidden; /* Ensure the image stays within the button */
}

.button img {
    width: 60%;
    height: auto;
    display: block; /* Prevent extra space below the image */
}

.button:hover {
    transform: scale(1.1); /* Grow the button on hover */
}

